}

.header__firstHeader .container {
  max-width: 1200px;
  margin: 0 auto;
  padding-left: 30px;
  padding-right: 30px;
}

.header__firstHeader .container .wrap {
  display: flex;
  flex-wrap: wrap;
}

.header__firstHeader .container .wrap .left-side {
  display: flex;
  flex-grow: 1;
  flex-basis: 520px;
  justify-content: space-around;
}

.header__firstHeader .container .wrap .right-side {
  flex-grow: 1;
  flex-basis: 520px;
  display: flex;
  justify-content: space-around;
}

